.wallet-container {
  width: auto;
  margin: auto;
  height: 97vh;
  /* background-color: #2C2F35; */
  text-align: center;
  font-family: Helvetica;
  background-image: url("../../assets/background1.jpg");
  background-size: cover; /* Ensures the image covers the entire container */
  background-position: center; /* Centers the image */
  background-repeat: no-repeat;
}

.paws-icon {
  padding: 1rem;
  justify-content: center;
  align-items: center;
}

.wallet-heading {
  font-size: 26px;
  font-weight: bold;
  font-family: Arial, sans-serif;
  padding-top: 1rem;
  color: white;
}

.wallet-subheading {
  font-size: 18px;
  color: #666;
  font-family: Arial, sans-serif;
  padding: 1rem;
  color: white;
}

.wallet-section {
  display: flex;
  padding: 2rem;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.wallet-connect-button button {
  border: 1px solid black;
  background: goldenrod;
}

.wallet-details {
  padding: 2rem;
  background-color: #2c2f35;
  border-radius: 8px;
  color: white;
}

.wallet-detail-item {
  font-size: 18px;
  color: white;
  font-family: Arial, sans-serif;
}

.wallet-public-key {
  word-wrap: break-word;
  color: goldenrod;
}

.wallet-connected {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.wallet-connected-button {
  background-color: goldenrod;
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  margin-top: 2rem;
}

.modal-wallet {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 100%;
  max-height: 100%;
  padding: 1rem;
  background-color: lightgray;
  border-radius: 0.5rem;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  transform: translateY(100%);
  transition: transform 0.3s ease-out;
}

.modal-overlay-wallet {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s ease-out;
}

.modal-wallet.open {
  transform: translateY(0);
}

.modal-overlay-wallet.open {
  opacity: 1;
}

.modal-overlay-wallet.closed {
  opacity: 0;
}

.modal-content-wallet {
  padding: 1rem;
  text-align: center;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
}

.wallet-connection-heading {
  padding-bottom: 1rem;
}

.wallet-modal-icon {
  width: 50px;
  margin-bottom: 0.25rem;
}

.wallet-information {
  border-radius: 0.5rem;
  border: 1px solid goldenrod;
  background: goldenrod;
  justify-content: center;
  align-items: center;
  display: flex;
  padding: 0.5rem;
}

.wallet-disconnection {
  border-radius: 0.5rem;
  border: 1px solid goldenrod;
  background: goldenrod;
  justify-content: center;
  align-items: center;
  display: flex;
  padding: 0.5rem;
  margin-right: 15px;
}
.modal-wallet-main-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-bottom: 2rem;
}

.wallet-public-key {
  color: black;
  font-weight: bolder;
}

.wallet-detail-icon {
  padding-right: 0.5rem;
}
.wallet-copy-icon {
  padding-left: 4rem;
}
